.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
}

.n-left{
    flex: 1;
    align-items:center;
    display: flex;
    gap: 2rem;
}

.n-name{
    font-size: 1.5rem;
    font-weight: 700;
}

.n-right{
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    font-weight: 400;
}
.n-list{
    flex: 10;
}
.n-list ul{
    display: flex;
    gap: 2rem;
    margin-right: 2.5rem;
}
.n-list ul li{
    list-style: none;
}
.n-list ul li:hover{
    cursor: pointer;
    color: var(--orange);
}
.n-button{
    flex: 2;
}
